.amountCalculator {
  background-color: hsl(230, 24%, 39%);
  color: #ffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}

.amountCalculator form {
  width: 90%;
}

.navButton {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
}

.navButton button {
  border: none;
  background-color: hsl(231, 57%, 91%);
  padding: 0.5rem;
  margin: 0.5rem;
  border-radius: 1rem;
  min-width: 5rem;
}

.navButton button:hover {
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .amountCalculator form {
    width: 95%;
  }

  .amountCalculator {
    padding: 1rem;
  }
}